<template>
  <div class="page-edm">
    <div class="btn-container">
      <el-button type="primary">
        模板预览<i class="el-icon-view el-icon--right"></i>
      </el-button>
      <el-button @click="goEditH5" type="primary">
        编辑H5<i class="el-icon-edit el-icon--right"></i>
      </el-button>
    </div>
    <el-form label-position="right" label-width="100px">
      <div class="item-group">
        <el-form-item label="EDM模板名称">
          <el-input :disabled="disPos" 
            v-model="templateName" 
            :class="{'input-item-disabled': disPos}"></el-input>
        </el-form-item>
        <el-form-item label="公司名称">
          <el-input :disabled="disPos" 
            v-model="company"
            :class="{'input-item-disabled': disPos}"></el-input>
        </el-form-item>
        <el-form-item label="发送称呼">
          <el-input :disabled="disPos" 
            v-model="sendTo"
            :class="{'input-item-disabled': disPos}"></el-input>
        </el-form-item>
        <el-form-item label="职位名称">
          <el-input :disabled="disPos" 
            v-model="positionName"
            :class="{'input-item-disabled': disPos}"></el-input>
        </el-form-item>
        <el-form-item label="职位介绍">
          <div class="item-content">
            <label>职位描述</label>
            <el-input :disabled="disPos" type="textarea" 
              v-model="positionDesc"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
          <div class="item-content">
            <label>职位要求</label>
            <el-input :disabled="disPos" type="textarea" 
              v-model="positionRequired"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
          <div class="item-content">
            <label>地点</label>
            <el-input :disabled="disPos" 
              v-model="address"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editPos" type="primary">
          {{btnPosText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>

      <div class="item-group">
        <el-form-item label="公司综合实力">
          <div class="item-content">
            <label>学霸团队</label>
            <el-input :disabled="disCom" 
              v-model="team"
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
          <div class="item-content">
            <label>名企大牛</label>
            <el-input :disabled="disCom" 
              v-model="smartPeople"
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
          <div class="item-content">
            <label>发展速度</label>
            <el-input :disabled="disCom" 
              v-model="extendSpeed"
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editCom" type="primary">
          {{btnComText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>

      <div class="item-group">
        <el-form-item label="员工个人回报">
          <div class="item-content">
            <label>职位晋升</label>
            <el-input :disabled="disEmp" 
              v-model="positionUpgrade"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
          <div class="item-content">
            <label>薪资倍增</label>
            <el-input :disabled="disEmp" 
              v-model="salaryInc"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
          <div class="item-content">
            <label>猎头目标</label>
            <el-input :disabled="disEmp" type="textarea" 
              v-model="huntTarget"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editEmp" type="primary">
          {{btnEmpText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        templateName: '标准EDM模板',
        company: '公司名称',
        sendTo: '简历ID候选人名称',
        content: '我是A企业的HR，目前正寻找一名出色的行政人员。我们非常中意你的背景，并愿意提供具有竞争力的薪酬福利，希望你能接受我的邀请。（确认后我会尽快联系你）',
        positionName: '职位名称',
        positionDesc: '的就赶快即可垃圾付款',
        positionRequired: '看大加加快好',
        address: '上海市马当路243784号',
        team: '学霸比例高达21%，团队实力完爆竞争对手',
        smartPeople: '23&的同事是有一线公司背景的资深大牛，顶级专家就在你身边',
        extendSpeed: '业务发展速度惊人，公司年新增员工数比行业平均水平高出10%',
        positionUpgrade: '从专员晋升到经理平均指需1.7年，大步走向人生巅峰',
        salaryInc: '每年加薪幅度比同行高17%，快速实现财务自由',
        huntTarget: '公司品牌让你身价大幅提升，更容易成为猎头目标，跳槽后薪资增长通常超过60%',
        disPos: true,
        disCom: true,
        disEmp: true
      }
    },
    computed: {
      btnPosText(){
        return this.disPos ? '编辑' : '保存'
      },
      btnComText(){
        return this.disCom ? '编辑' : '保存'
      },
      btnEmpText(){
        return this.disEmp ? '编辑' : '保存'
      }
    },
    methods: {
      goEditH5(){
        this.$router.push({ name: 'edm-h5' }) 
      },
      editPos(){
        this.disPos = !this.disPos
      },
      editCom(){
        this.disCom = !this.disCom
      },
      editEmp(){
        this.disEmp = !this.disEmp
      }
    }
  }
</script>

<style lang="stylus">
  @import './Edm.styl'
</style>


