<template>
  <div class="page-task">
    <el-steps :active="1" :space="200">
      <el-step title="第一步" description="确认发送目标"></el-step>
      <el-step title="第二步" description="确认发送的内容与时间"></el-step>
    </el-steps>
    <el-form label-position="left" label-width="60px">
      <el-form-item label="职位">
        <el-input @change="fetchInfo" v-model="posID" placeholder="请输入职位ID"></el-input>
      </el-form-item>
      <el-form-item label="候选人">
        <!-- here we must use `el-row` to wrapped `e-col`, 
          because the top container is a `el-row`  -->
        <el-row>
          <el-col :span="8">
            <el-select v-model="value">
              <el-option v-for="(item, index) in options"
                :label="item.label"
                :value="item.value"
                :disabled="item.disabled"
                :key="index">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="8">
            <el-input :disabled="true" placeholder="请输入职位ID"></el-input>
          </el-col>
          <el-col :span="8">
            <el-upload
              :action="action"
              :on-success="handleSucc"
              :before-upload="chenkFile"
              :file-list="fileList"
              name="Filedata">
              <el-button type="primary">上传简历ID</el-button>
              <div slot="tip" class="upload-tip">目前只支持.txt,.xlsx,.xls,.csv文件</div>
            </el-upload>
          </el-col>
        </el-row>
      </el-form-item>
    </el-form>
    <el-button class="btn-next" type="primary"
      @click="goNext" :disabled="!nextBtnActive">下一步</el-button>
  </div>
</template>

<script>
  import debounce from 'debounce'

  export default {
    data(){
      return {
        options: [
          { label: '自定义', value: '0' },
          { label: '匹配度', value: '1', disabled: true },
          { label: '搜索', value: '2', disabled: true }
        ],
        value: '0',
        posID: '',
        filePath: '',
        fileList: [],
        // use element-ui build-in upload ajax,
        // so we set full url in development mode here 
        action: process.env.NODE_ENV === 'production' 
          ? 'http://edmserver.cheng95.com/api/uploadfile' : '/uploadfile'
      }
    },
    computed: {
      nextBtnActive(){
        return !!this.posID && !!this.value
      }
    },
    methods: {
      // note: keyword `this` refer to
      fetchInfo: debounce(function(){
          this.$axios.get(`/positioninfo?id=${this.posID}`).then(info => {
            if(!info) return

            this.$notify({
              type: 'info',
              title: `职位ID：${this.posID}`,
              message: `公司名称： ${info.corporation_name}；职位名称：${info.name}`, 
              duration: 0
            })
          })
        }, 1000),

      chenkFile({ type, size }){
        if(type !== 'text/plain' 
            && type !== 'text/csv'
            && type !== 'application/vnd.ms-excel' ){
          this.$message.error('目前只支持.txt，.xlsx，.xls，.csv文件')
          return false
        }
        if(size / 1000 ** 2 > 5){
          this.$message.error('目前最大只能上传5M的文件')
          return false
        }
        if(size === 0){
          this.$message.error('上传的文件不能为空')
          return false
        }
        return true
      },
      handleSucc(res, fileList){
        const { code, errormsg, info } = res
        if(code != 0) return this.$message.error(errormsg)

        this.filePath = info.path
      },
      goNext(){
        this.$router.push({ name: 'task-send', query: {
          position_id: this.posID,
          Filedata: this.filePath
        } })
      }
    }
  }
</script>

<style lang="stylus">
  @import './Task.styl'
</style>


