import Vue from 'vue'
import Router from 'vue-router'
import Index from '~/Index'
import Hello from '~/Hello'
import Signin from '~/Signin'
import Task from '~/Task'
import TaskSend from '~/Task-Send'
import Edm from '~/Edm'
import Edmh5 from '~/Edm-h5'

import TplWeb from '~/Tpl-Web'


Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      component: Signin
    },
    {
      path: '/signin',
      name: 'signin',
      component: Signin
    },
    {
      path: '/index',
      name: 'index',
      component: Index,
      meta: { requiresAuth: true },
      children: [
        {
          path: '',
          component: Task
        },
        {
          path: 'task',
          name: 'task',
          component: Task
        },
        {
          path: 'task-send',
          name: 'task-send',
          component: TaskSend
        },
        {
          path: 'edm',
          name: 'edm',
          component: Edm
        },
        {
          path: 'edm-h5',
          name: 'edm-h5',
          component: Edmh5
        },
        {
          path: 'tpl-web',
          name: 'tpl-web',
          component: TplWeb
        }
      ]
    }
  ]
})



