<template>
  <div class="hello">
    <el-button @click="showMessage">默认按钮</el-button>
    <el-button @click="showMessageBox">默认按钮</el-button>
    <el-button @click="showNotification">默认按钮</el-button>
    <el-select v-model="value" placeholder="请选择">
      <el-option v-for="(item, index) in options"
        :label="item.label"
        :value="item.value"
        :key="index">
      </el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data() {
    return {
      options: [{
        value: '选项1',
        label: '黄金糕'
      }, {
        value: '选项2',
        label: '双皮奶'
      }, {
        value: '选项3',
        label: '蚵仔煎'
      }, {
        value: '选项4',
        label: '龙须面'
      }, {
        value: '选项5',
        label: '北京烤鸭'
      }],
      value: ''
    }
  },
  methods: {
    showMessage(){
      this.$notify({
        title: '标题',
        message: '基督教副科级',
        type: 'success'
      })
    },
    showMessageBox(){
      this.$confirm('测试confirm')
    },
    showNotification(){
      this.$alert('测试alert')
    }
  }
}
</script>



