import 'reset.css'
import 'css/base.css'

import Vue from 'vue'
import App from './App'
import router from './router'
Vue.config.productionTip = false
Vue.config.performance = process.env.NODE_ENV !== 'production' ? true : false

import axios from 'axios'
// transform post data for cross site
import qs from 'qs'
import { parseCookie } from 'js/common'

import { 
  Row, 
  Col, 
  Button, 
  Select, 
  Option, 
  Menu,
  MenuItem,
  Form,
  FormItem,
  Input,
  Progress,
  Steps,
  Step,
  Loading,
  MessageBox,
  Notification,
  Message,
  Upload,
  Table,
  TableColumn
 } from 'element-ui'

;([
  Row, 
  Col, 
  Button, 
  Select, 
  Option, 
  Menu,
  MenuItem,
  Form,
  FormItem,
  Input,
  Progress,
  Steps,
  Step,
  Upload,
  Table,
  TableColumn
]).forEach(c => Vue.use(c))

const install = function(Vue, opts = {}) {
  if (install.installed) return

  Vue.prototype.$loading = Loading.service
  Vue.prototype.$msgbox = MessageBox
  Vue.prototype.$alert = MessageBox.alert
  Vue.prototype.$confirm = MessageBox.confirm
  Vue.prototype.$prompt = MessageBox.prompt
  Vue.prototype.$notify = Notification
  Vue.prototype.$message = Message
  Vue.prototype.$axios = axios
}

install(Vue)

/* eslint-disable no-new */
const vm = new Vue({
  el: '#app',
  router,
  render: h => h(App)
})


// identify a use has logged or not
const token = parseCookie(document.cookie, 'edm_token')
/*router.beforeEach((to, from, next) => {
  if(to.matched.some(record => {
    return record.meta.requiresAuth
  })){
    if(!token){
      vm.$message.warning('请选登录!')
      return router.push({ name: 'signin' })
    }
    next()
  }else{
    next()
  }
})*/

axios.defaults.baseURL = process.env.NODE_ENV === 'production' 
  ? 'http://edmserver.cheng95.com'
  : ''

axios.interceptors.request.use(config => {
  vm.loading = vm.$loading('加载中...')

  if(process.env.NODE_ENV !== 'production'){
    config.url = config.url.replace(config.baseURL, config.baseURL + '/api')
    // replace default JSON format with application/x-www-form-urlencode
    config.data = qs.stringify(config.data)
  }
  return config
}, error => {
  vm.loading.close()
  return Promise.reject(error)
})

axios.interceptors.response.use(response => {
  vm.loading.close()

  const { code, errormsg, info } = response.data

  if(code != 0 && errormsg){
    vm.$message.error(errormsg)
    return null
  }

  return info
}, error => {
  vm.loading.close()
  vm.$message.error(error)
  return null
})


