export const parseCookie = (cookie, key) => {
  const ret = Object.create(null)

  String(cookie).replace(/;\s+/mg, '').split(';').forEach(value => {
    const pairs = value.split('=')
    ret[pairs[0]] = pairs[1]
  })

  if(key) return ret[key]

  return ret
}

export const parseHtml = escapedStr => {
    return escapedStr.replace(/&amp;/g, '&')
      .replace(/&lt;/g, '<')
      .replace(/&gt;/g, '>')
      .replace(/&quot;/g, '"')
      .replace(/&#039;/g, "'")
}


