<template>
  <div class="page-task-send">
    <div class="theme-data" v-if="showThemeData">
      <el-table :data="themeData" 
        max-height="400" border>
        <el-table-column
          label="主题"
          prop="title"
          width="250"></el-table-column>
        <el-table-column
          label="操作"
          width="100">
          <template scope="scope">
           <el-button
             @click.native.prevent="delTableRow(scope.$index, themeData)"
             type="text"
             size="small">
             移除
           </el-button>
         </template>
        </el-table-column>
      </el-table>
    </div>
  
    <el-steps :active="2" :space="200">
      <el-step ref="firstStep" title="第一步" description="确认发送目标"></el-step>
      <el-step title="第二步" description="确认发送的内容与时间"></el-step>
    </el-steps>

    <el-row :gutter="10">
      <!-- left -->
      <el-col :span="15" v-html="tplHtml">
        <!-- <tpl-web></tpl-web> -->
      </el-col>
      <!-- right -->
      <el-col :span="8" class="layout-right">
        <div class="right-content">
          <el-form label-position="top" label-width="80px">
            <el-form-item label="发送主题">
              <el-input ref="title" v-model.trim="email_title" type="textarea"
                placeholder="可一次性输入多个主题，回车换行分割主题"
                :autosize="{minRows: 4, maxRows: 8}"></el-input>
            </el-form-item>
            <el-form-item label="发送模板">
              <el-select @change="fetchTpl" v-model.trim="email_templete_id">
                <el-option v-for="(item, index) in options"
                  :value="item.value"
                  :label="item.label"
                  :key="index">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="试发送邮箱">
              <el-input v-model.trim="send_email" type="textarea" 
                placeholder="可同时发送多个"
                resize="none"></el-input>
            </el-form-item>
            <el-form-item label="试发送职位ID">
              <el-input :disabled="true" v-model.trim="position_id"></el-input>
            </el-form-item>
            <el-form-item label="试发送简历ID">
              <el-input placeholder="请先上传简历ID"
                :disabled="true" 
                v-model="Filedata"></el-input>
            </el-form-item>
          </el-form>
          <el-button @click="send" 
            :disabled="!tryBtnActive"
            class="btn-try" type="primary">试发送</el-button>
        </div>
        <el-button 
          :disabled="!sendBtnActive" 
          @click="send(true)"
          class="btn-send">立即发送</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import TplWeb from './Tpl-Web.vue'
  import { parseHtml } from 'js/common'
  export default {
    data(){
      return {
        options: [
          { label: '标准版EDM模板', value: 0 }
        ],
        email_title: '',
        email_templete_id: 0,
        // a flag field
        // post ajax with it, represent it's a try send
        send_email: '',
        position_id: '',
        Filedata: '',
        trySendSucc: false,
        themeData: [],
        tplHtml: ''
      }
    },
    computed: {
      tryBtnActive(){
        return this.email_title && !!this.send_email
      },
      sendBtnActive(){
        return this.email_title && this.trySendSucc
      },
      showThemeData(){
        return !!this.email_title
      }
    },
    methods: {
      delTableRow(index, rows){
        let { title } = rows.splice(index, 1)[0]
        this.email_title = this.email_title.replace(new RegExp(`${title}\n*`), '')
      },
      send(isImmediate){
        const data = {
          email_title: this.email_title,
          email_templete_id: this.email_templete_id,
          send_email: this.send_email,
          position_id: this.position_id,
          Filedata: this.Filedata
        }
      
        isImmediate && delete data.send_email

        this.$axios.post('/addsendemail', data).then(info => {
          this.$message.success('试发送成功')
        })
      },
      goBack(){
        this.$confirm('离开本页面，将丢失已经填好的信息。您确定要离开吗？', '温馨提示', {
          type: 'warning'
        }).then(() => {
          this.$router.back()
        })
      },
      fetchTpl(){
        this.$axios.get(`/gettemplate?position_id=${this.position_id}`)
          .then(info => {
            if(!info) return

            this.tplHtml = parseHtml(info.html)
          })
      }
    },
    created(){
      const { Filedata, position_id } = this.$route.query
      this.Filedata = Filedata
      this.position_id = position_id

      // init email template
      this.fetchTpl()
    },
    mounted(){
      // elementUI not provides build-in events,
      // so we operate dom
      this.$refs.firstStep.$el
        .getElementsByClassName('el-step__icon')[0]
        .addEventListener('click', () => {
          this.goBack()
        })

      this.$refs.title.$el
        .addEventListener('keyup', (e) => {
          if(e.keyCode === 13){
            this.themeData = this.email_title.split(/\n/gm).map(val => {
              return { title: val }
            })
          }
        })
    },
    components: { TplWeb }
  }
</script>

<style lang="stylus">
  @import './Task-Send.styl'
</style>


