<template>
  <div class="page-edm">
    <div class="btn-container">
      <el-button type="primary">
        预览H5<i class="el-icon-view el-icon--right"></i>
      </el-button>
      <el-button @click="spawnLink" type="primary">
        生成链接<i class="el-icon-upload el-icon--right"></i>
      </el-button>
    </div>
    <el-form label-position="right" label-width="100px">
      <div class="item-group">
        <el-form-item label="H5标题">
          <el-input v-model="h5Name"
            :class="{'input-item-disabled': disPos}"></el-input>
        </el-form-item>
        <el-form-item label="隐藏实力惊人">
          <div class="item-content">
            <label>名企牛人背景</label>
            <el-input type="textarea" v-model="spBackdrop"
              :disabled="disPos"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
          <div class="item-content">
            <label>一流黄埔军校</label>
            <el-input type="textarea" v-model="positionRequired"
              :disabled="disPos"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
          <div class="item-content">
            <label>工作环境稳定</label>
            <el-input v-model="address"
              :disabled="disPos"
              :class="{'input-item-disabled': disPos}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editPos" type="primary">
          {{btnPosText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>

      <div class="item-group">
        <el-form-item label="行业表现出众">
          <div class="item-content">
            <label>学霸团队</label>
            <el-input v-model="team"
              :disabled="disCom" 
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
          <div class="item-content">
            <label>名企大牛</label>
            <el-input v-model="smartPeople"
              :disabled="disCom"
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
          <div class="item-content">
            <label>发展速度</label>
            <el-input v-model="extendSpeed"
              :disabled="disCom"
              :class="{'input-item-disabled': disCom}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editCom" type="primary">
          {{btnComText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>

      <div class="item-group">
        <el-form-item label="员工收益不菲">
          <div class="item-content">
            <label>职位晋升</label>
            <el-input v-model="positionUpgrade"
              :disabled="disEmp"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
          <div class="item-content">
            <label>薪资倍增</label>
            <el-input v-model="salaryInc"
              :disabled="disEmp"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
          <div class="item-content">
            <label>猎头目标</label>
            <el-input type="textarea" v-model="huntTarget"
              :disabled="disEmp"
              :class="{'input-item-disabled': disEmp}"></el-input>
          </div>
        </el-form-item>
        <el-button @click="editEmp" type="primary">
          {{btnEmpText}}<i class="el-icon-edit el-icon--right"></i>
        </el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        h5Name: '标准EDM模板',
        company: '公司名称',
        content: '我是A企业的HR，目前正寻找一名出色的行政人员。我们非常中意你的背景，并愿意提供具有竞争力的薪酬福利，希望你能接受我的邀请。（确认后我会尽快联系你）',
        team: '就的看法',
        positionRequired: '看大加加快好',
        address: '上海市马当路243784号',
        smartPeople: '借记卡水电费会计师',
        spBackdrop: '23&的同事是有一线公司背景的资深大牛，顶级专家就在你身边',
        extendSpeed: '业务发展速度惊人，公司年新增员工数比行业平均水平高出10%',
        positionUpgrade: '从专员晋升到经理平均指需1.7年，大步走向人生巅峰',
        salaryInc: '每年加薪幅度比同行高17%，快速实现财务自由',
        huntTarget: '公司品牌让你身价大幅提升，更容易成为猎头目标，跳槽后薪资增长通常超过60%',
        disPos: true,
        disCom: true,
        disEmp: true
      }
    },
    computed: {
      btnPosText(){
        return this.disPos ? '编辑' : '保存'
      },
      btnComText(){
        return this.disCom ? '编辑' : '保存'
      },
      btnEmpText(){
        return this.disEmp ? '编辑' : '保存'
      }
    },
    methods: {
      editPos(){
        this.disPos = !this.disPos
      },
      editCom(){
        this.disCom = !this.disCom
      },
      editEmp(){
        this.disEmp = !this.disEmp
      },
      spawnLink(){
        this.$prompt('H5链接', '温馨提示', {
          confirmButtonText: '复制链接',
          showCancelButton: false,
          closeOnClickModal: false,
          inputValue: 'http://uimg.ifchange.com/special/1/195/index.html'
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: '复制成功'
          })
          console.log(value)
        })
      }
    }
  }
</script>

<style lang="stylus">
  @import './Edm.styl'
</style>


